import React from 'react';

class UGBasicCivilEngineering extends React.Component{

	render(){

		return(

			<div>

			    <h3>BASIC CIVIL ENGINEERING</h3>

			    <hr/>
			    <div className='wrapper'>
				    <h4>
				    OBJECTIVE
				    </h4>
				    <p>
					To provide an exposure to the learner on the various 
					components of civil engineering like
					construction materials and techniques, 
					surveying and mechanics of structures.
					</p>
				</div>
				<hr/>

			    <div className='wrapper'>
				    <h4>
				    UNIT  I
				    </h4>
				    <p>
					<strong>Civil Engineering Materials: </strong> 
					Branches of Civil Engineering  Civil Engineering materials 
					 stones, Bricks, Steel, Cement,
					Concrete, Timber and plastics  
					different types.
					</p>
				</div>
				<hr/>
				
				<div className='wrapper'>
					<h4>
				    UNIT  II
				    </h4>
					<p>
					<strong>Surveying: </strong> 
					Types, classification, measurement of distances 
					 angles  determination of areas 
					Levelling  Simple problems  Total station 
					 Remote Sensing.
					</p>
				</div>
				<hr/>

				<div className='wrapper'>
					<h4>
				    UNIT - III
				    </h4>
					<p>
					<strong>Building Components: </strong> 
					Foundations  Bearing capacity  Requirement of good 
					foundations  Types, Stone masonry.
					Brick Masonry  Beams, columns, Lintels, roofing 
					 Flooring  Different types only  Interior
					design  landscaping.
					</p>
				</div>
				<hr/>

				<div className='wrapper'>
					<h4>
				    UNIT - IV
				    </h4>
					<p>
					<strong>Mechanics and Structures: </strong> Forces, stress, strain, Elasticity  simple problems  
					Bridges, dams, roads 
					different types  Railways-permanent way - components.
					</p>
				</div>
				<hr/>

				<div className='wrapper'>
					<h4>
				    TEXTBOOKS
				    </h4>
					<p>
					1. M. S. Palanichamy, C. Shanmugham, 
					Basic Civil Engineering and Basic Mechanical
					Engineering, Tata McGrawHill Publishing 
					Company Ltd. , 2000.
					</p>
				</div>
				<hr/>

				<div className='wrapper'>
					<h4>
				    REFERENCES
				    </h4>
					<p>
					1. Natarajan K. V. , Basic Civil Engg. , Dhanalakshmi Publications, Chennai, 2005 <br/>
					2. Ramamrutham, Basic Civil Engineering, Dhanpat Rai Publishing Company (P), Ltd. , 1999. <br/>
					3. Satheesh Gopi, Basic Civil Engineering Pearson Publications, 2010.
					</p>
				</div>
				<hr/>

			</div>
		)
	}
}

export default UGBasicCivilEngineering;