import React from 'react';

class UGEngineeringMechanics extends React.Component{

	render(){

		return(

			<div>

			    <h3>ENGINEERING MECHANICS</h3>

			    <hr/>
			    <div className='wrapper'>
				    <h4>
				    OBJECTIVE
				    </h4>
				    <p>
					The objective of this fundamental course in 
					mechanics is to enhance the analytical and logical
					problem solving skills of the student. The basic 
					concepts dealt in this course form the
					cornerstone of advanced topics such as solid 
					mechanics, structural mechanics, fluid mechanics,
					aero-mechanics, bio-mechanics, machine design 
					and electrical/robotics devices.
					</p>
				</div>
				<hr/>

			    <div className='wrapper'>
				    <h4>
				    UNIT  I
				    </h4>
				    <p>
					<strong>Equilibrium of Particles: </strong> 
					Frames of reference - Force Systems - 
					Resolution and addition of forces, 
					resultant of several concurrent forces, Forces in 3D, 
					Equations of equilibrium of particle in 2D and 3D - 
					Lames theorem.
					</p>
				</div>
				<hr/>
				
				<div className='wrapper'>
					<h4>
				    UNIT  II
				    </h4>
					<p>
					<strong>Equilibrium of Rigid Bodies: </strong>
					Moment of a force about a point  moments 
					and couples  Varignons theorem - equivalent
					force systems-resultant of non-concurrent force 
					systems  parallel forces. Equations of equilibrium 
					of rigid bodies in 2D and 3D. 
					<strong> Applications: </strong>
					Beams  types of supports, loads and reactions. 
					Concept of internal forces. Analysis of pin jointed 
					trusses  Method of sections and joints.
					</p>
				</div>
				<hr/>

				<div className='wrapper'>
					<h4>
				    UNIT - III
				    </h4>
					<p>
					<strong>Friction: </strong> 
					Laws of friction  simple contact friction - cone of friction 
					 belt friction  wedge friction  screw jack. 
					<strong> Properties of areas: </strong>
					Centroid of line, area and volume  
					Composite areas  hollow sections 
					Center of gravity of regular solids  cone  
					hemisphere. Second moment of areas  Parallel axis
					theorem  perpendicular axis theorem. Method 
					of integration for M.I of areas  triangle, circle,
					semi circle, quadrant of a circle. Composite 
					sections  Tee section, I section, Z section.
					</p>
				</div>
				<hr/>

				<div className='wrapper'>
					<h4>
				    UNIT - IV
				    </h4>
					<p>
					<strong>Dynamics of Particles: </strong> 
					Displacement, velocity and acceleration and 
					their relationship  rectilinear and Curvilinear
					motion - rectangular components, Tangent 
					and normal acceleration. Motion in a circular path.
					Newtons laws  Applications. Work energy 
					principle- impulse and momentum principle. Applications.
					</p>
				</div>
				<hr/>

				<div className='wrapper'>
					<h4>
				    TEXTBOOKS
				    </h4>
					<p>
					1. Beer F. P. and Johnston E. R. , Vector Mechanics for Engineers 
					 Statics and Dynamics, McGraw Hill International Book Company, 2002.<br/>
					2. Rajasekaran S. and Sankarasubramanian G. , Fundamental of Engineering 
					Mechanics, Vikas Publishing House Pvt. Ltd. , 2008.
					</p>
				</div>
				<hr/>

				<div className='wrapper'>
					<h4>
				    REFERENCES
				    </h4>
					<p>
					1. Ferdinand L. Singer, Engineering Mechanics 
					(Statics and Dynamics), Harper Row Publishers. 2007.<br/>
					2. Meriam J. L. and Kraige L. G. , Engineering Mechanics 
					(Statics and Dynamics), John Wiley and Sons. 2001.<br/>
					3. Shames I. H. , Engineering Mechanics 
					(Statics and Dynamics), Prentice  Hall of India - New Delhi, 2004.<br/>
					4. Timoshenko S. P. and Young D.H. , Engineering Mechanics, 
					McGraw Hill Book Co. Ltd. 2003.
					</p>
				</div>
				<hr/>

			</div>
		)
	}
}

export default UGEngineeringMechanics;