import React from 'react';

class UGEngineeringMathematics2 extends React.Component{

	render(){

		return(

			<div>

			    <h3>ENGINEERING MATHEMATICS - II</h3>

			    <hr/>
			    <div className='wrapper'>
				    <h4>
				    OBJECTIVE
				    </h4>
				    <p>
					To help the learners understand various mathematical techniques in ordinary
					differential equations, matrix theory, vector differentiation and integration, three
					dimensional analytical geometry.
					</p>
				</div>
				<hr/>

			    <div className='wrapper'>
				    <h4>
				    UNIT  I
				    </h4>
				    <p>
					<strong>Ordinary Differential Equations:</strong> First order first degree ODEs - 
					Bernoullis Differential equation 
					 Exact ODEs - First order higher
					degree ODEs - solvable for p,x,y  Clairauts form  
					Application in Electrical circuits  current
					inductive circuits  capacitance circuits  orthogonal trajectories 
					 solving second and higher
					order ordinary differential equations with constant coefficients 
					 particular integrals of various
					functions  Eulers differential equations with variable coefficients 
					 simultaneous differential
					equations with constant coefficients 
					 method of variation of parameters.
					</p>
				</div>
				<hr/>
				
				<div className='wrapper'>
					<h4>
				    UNIT  II
				    </h4>
					<p>
					<strong>Matrix Algebra: </strong> Introduction  Inverse of a matrix by elementary transformation  
					Linear independence and dependence of vectors - 
					Eigen values and Eigen vectors  properties of Eigen values and Eigen
					vectors with proofs  Cayley Hamilton theorem (without proof) 
					 Finding higher powers and Inverse of the matrix by using 
					Cayley Hamilton theorem  Similarity transformation 
					Diagonalisation of a matrix by similarity transformation  
					Orthogonal matrix  Diagonalisation of a matrix by orthogonal 
					reduction  Quadratic Forms  Nature of Quadratic forms  reduction of
					Quadratic form to Canonical form.
					</p>
				</div>
				<hr/>

				<div className='wrapper'>
					<h4>
				    UNIT - III
				    </h4>
					<p>
					<strong>Vector Calculus:</strong> Vector differentiation - Gradient , Divergence and Curl  Geometrical and Physical
					Interpretation (without proof)  Irrotational and Solenoidal vector fields  vector operator
					identities without proof  Vector integration definition  simple problems on line, surface and
					volume integrals  Greens theorem, Stokes theorem and Gauss Divergence theorem (without
					proof)  verifications and applications.
					</p>
				</div>
				<hr/>

				<div className='wrapper'>
					<h4>
				    UNIT - IV
				    </h4>
					<p>
					<strong>Analytical Geometry of Three Dimensions: </strong> The Plane  
					Angle between the planes  The Straight Line  Symmetrical form of the equations
					of a Line  Transformation of the equations of a line from unsymmetrical form to the symmetrical
					form  Coplanar Lines  Skew Lines  Shortest distance between two lines  The Sphere 
					Equation of tangent plane to a Sphere at a given point on it  Orthogonal Spheres.
					</p>
				</div>
				<hr/>

				<div className='wrapper'>
					<h4>
				    TEXTBOOKS
				    </h4>
					<p>
					1. Engineering Mathematics II , Pearson Publications, (Revised Edition 2014).
					</p>
				</div>
				<hr/>

				<div className='wrapper'>
					<h4>
				    REFERENCES
				    </h4>
					<p>
					1. Engineering Mathematics Volume II, M. K. Venkataraman, National publishing
					Company, India 2001. <br/>
					2. Engineering Mathematics for first year, T. Veerarajan, 5th Edition, 
					Tata McGraw Hill, 2006. <br/>
					3. Engineering Mathematics Volume III, P. Kandasamy and others, S. Chand, 2006. <br/>
					4. Advanced Engineering Mathematics, Erwin Kreyszig, 8th Edition, John Wiley and
					Sons (ASIA) pvt limited 2003. <br/>
					5. Advanced Calculus, Volume I, S. Arumugam, A. Thangapandi Issac, New
					Gamma Publishing House, 2007. <br/>
					6. Algebra, Narayanan , T. K. Manicavachagom Pillai, Ganapathy, S. V. Publishers, 2005.
					</p>
				</div>
				<hr/>

			</div>
		)
	}
}

export default UGEngineeringMathematics2;