import React from 'react';

class UGEngineeringMathematics1 extends React.Component{

	render(){

		return(

			<div>

			    <h3>ENGINEERING MATHEMATICS-I</h3>

			    <hr/>
			    <div className='wrapper'>
				    <h4>
				    OBJECTIVE
				    </h4>
				    <p>
					To help the learner in understanding various 
					mathematical techniques in trigonometry,
					calculus , maxima - minima of two variables 
					and theory of equations.
					</p>
				</div>
				<hr/>

			    <div className='wrapper'>
				    <h4>
				    UNIT  I
				    </h4>
				    <p>
					<strong>Trigonometry: </strong> 
					Expansions of sin n and cos n in powers 
					of sin and cos  Expansions of sin n and 
					cosn in terms of sines and cosines of multiples 
					of   Hyperbolic and Inverse hyperbolic functions 
					 Logarithm of complex numbers  separation of 
					complex functions into real and imaginary parts  simple problems.
					</p>
				</div>
				<hr/>
				
				<div className='wrapper'>
					<h4>
				    UNIT  II
				    </h4>
					<p> 
					<strong>Theory of Equations: </strong>
					Introduction  surds and irrational roots  simple problems  equations
					whose roots are in A.P, G.P and in H.P  Relations between the roots and coefficients 
					Symmetric functions  Formation of equations  Decreasing and Increasing the roots 
					Transformation of equations  Reciprocal equations  Descartes rule of Signs.
					<strong> Algebra: </strong> 
					Binomial, Exponential and Logarithmic Series(without proof)  Problems on
					summation, coefficient and approximations.
					</p>
				</div>
				<hr/>

				<div className='wrapper'>
					<h4>
				    UNIT - III
				    </h4>
					<p>
					<strong>Differential Calculus: </strong>
					The nth derivative of a function  Leibnitz theorem  Simple problems.
					Curvature of a curve  Radius of Curvature (Cartesian, Polar, 
					Paramatric and Implicit form)  Evolutes  Involutes  Envelopes 
					(one parameter and two parameter)  Evolute as the
					envelope of normals.
					<strong> Functions of Several Variables: </strong> 
					Maxima and Minima of functions of two
					variables (proofs of theorems are not included) 
					 Constrained Maxima and Minima 
					Lagranges method of multipliers.
					</p>
				</div>
				<hr/>

				<div className='wrapper'>
					<h4>
				    UNIT - IV
				    </h4>
					<p>
					<strong>Improper Integrals: </strong>
					Concept of improper integrals with examples  
					Definition of Beta and Gamma integrals  Relation 
					between them  Properties of Beta and Gamma 
					integrals with proofs  Evaluation of definite integrals 
					in terms of Beta and Gamma integrals  Simple
					applications (evaluation of double and triple integrals).
					<strong> Multiple Integrals: </strong> 
					Double Integrals  Evaluations  
					Change of order of integration  Triple integrals 
					(problems involving Jacobians are not included) 
					 Simple applications.
					</p>
				</div>
				<hr/>

				<div className='wrapper'>
					<h4>
				    TEXTBOOKS
				    </h4>
					<p>
					Engineering Mathematics I, SASTRA University Publications, (Revised Edition 2014).
					</p>
				</div>
				<hr/>

				<div className='wrapper'>
					<h4>
				    REFERENCES
				    </h4>
					<p>
					1. Engineering Mathematics for first year,T. Veerarajan, 
					5th Edition, Tata McGraw Hill, 2006. <br/>
					2. Advanced Engineering Mathematics, 
					Erwin Kreyszig, 8th Edition, John Wiley and
					Sons (ASIA) Pvt limited 2003.
					</p>
				</div>
				<hr/>

			</div>
		)
	}
}

export default UGEngineeringMathematics1;